.header {
    height: 60px;
    background-color: #333;
    color: white;
}

.headerWrapper {
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
}

.headerLogo {
    padding: 8px 12px;
    font-size: 24px;
}

.headerRight {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.headerRight svg {
    cursor: pointer;
    position: relative;
}

.loginUser {
    margin-right: 32px;
}

.accordionMenu {
    position: absolute;
    top: 5px;
    right: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px 10px;
    max-height: 0;
    z-index: 9999;
}

.accordionMenu button {
    font-size: 16px;
    margin: 10px 0;
    background-color: white;
    cursor: pointer;
}

.accordionMenu hr {
    width: 100%;
    margin: 5px 0;
}

.accordionMenu.closed {
    visibility: hidden;
}

.accordionMenu:not(.closed) {
    max-height: 200px;
    visibility: visible;
}