.account {
    height: 100vh;
}

.accountWrapper {
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.accountLeftHead,
.accountRightHead {
    font-size: 26px;
    text-align: center;
}

.accountLeftBox,
.accountRightBox {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    box-shadow: 0px 0px 15px -5px #3d83b8;
    border-radius: 10px;
    padding: 48px 16px;
}

.editBox {
    display: flex;
    flex-direction: column;
}

.editInput {
    display: flex;
    flex-direction: column;
}

.newNikname {
    padding: 4px 16px;
    margin-bottom: 16px;
    font-size: 16px;
}

.newNikname:focus {
    outline: none;
}

.newNiknameButton {
    cursor: pointer;
    padding: 6px 0;
    color: #222;
}

.deleteAccountButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 202px;
    height: 31.5px;
    cursor: pointer;
}

.accountButton {
   transition: all 0.3s;
}

.accountButton:hover {
    background-color: #222;
    color: aliceblue;
}