a {
    text-decoration: none;
    color: inherit;
}

.sidebar {
    flex: 2;
    height: 100vh;
    border-right: 1px black solid;
    background-color: rgb(242, 245, 247);
}

.sidebarWrapper {
    overflow-y: auto; /* 必要に応じてスクロールを表示 */
    height: 90%; /* 親要素の高さに合わせる */
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.threadHead {
    font-size: 24px;
    margin-bottom: 24px;
}

.threadsList {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    overflow-y: auto; /* スクロールを表示 */
    max-height: 100%; /* 親要素に合わせて高さを制限 */
}



.threadItem {
    width: 100%;
    font-weight: 500;
    margin-bottom: 32px;
    cursor: pointer;
    transition: all 0.3s;
    padding: 5px 0;
}

.sidebarThreadTitle {
    font-size: 18px;
}

.sidebarThreadCreatedAt {
    font-size: 12px;
}

.threadItem:hover {
    background-color: black;
    color: white;
}

.createThread {
    border: 1.5px black solid;
    border-radius: 8px;
    padding: 16px 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
}

.createThreadInput {
   width: 100%;
   font-size: 16px;
   padding: 5px 0;
   margin-bottom: 12px;
}

.createThreadInput:focus {
    outline: none;
}


.createThreadButton {
    width: 100%;
    cursor: pointer;
    font-weight: 550;
    padding: 8px 4px;
    border: 2px black solid;
    font-size: 16px;
    border-radius: 16px;
    transition: all 0.3s;
}

.createThreadButton:hover {
    background-color: black;
    color: white;
}