.posts {
    padding: 4px 12px;
}

.postItem {
    margin-bottom: 8px;
}

.postInfo {
    display: flex;
    margin-bottom: 6px;
}

.deleteEditImage {
    width: 5%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-left: 16px;
    cursor: pointer;
}

.postUserName-own {
    color: red;
}

.postUserName {
    font-weight: bold;
    font-size: 16px;
}

.postEditInput {
    margin: 6px 0;
    width: 100%;
    display: flex;
    align-items: center;
}

.postEditInput input {
    font-size: 16px;
    padding: 4px 0;
    width: 100%;
}

.postEditInput input:focus {
   outline: none;
}



.postText {
    font-size: 20px;
}