.thread {
    flex: 10;
}

.selectTread {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
}

.threadWrapper {
    height: 90vh;
    margin: 16px 128px;
    position: relative;
}

.threadHeader {
    display: flex;
    height: 50px;
    align-items: center;
    justify-content: space-between;
}

.threadTitle {
    font-size: 32px;
}

.threadHeaderImage {
    cursor: pointer;
}

.threadTitle h2 {
    display: flex;
    align-items: center;
}

.threadContainer {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    max-height: 80vh;
    height: 80vh;
    margin-bottom: 64px;
    border: 3px black solid;
    border-radius: 16px;
}

.threadPostInput {
    position: absolute;
    bottom: -40px;
    left: 0;
    width: 90%;
    height: 25px;
    font-size: 20px;
    padding: 5px 5px;
    resize: none;
}

.threadPostInput:focus {
    outline: none;
}

.threadSubmitButton {
    position: absolute;
    bottom: -40px;
    right: 0;
    font-size: 16px;
    text-align: center;
    cursor: pointer;
    padding: 6px 10px;
    border: 1px black solid;
}
