.login {
    width: 100vw;
    height: 100vh;
    background-color: rgb(237, 237, 237);
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginWrapper {
    width: 80%;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loginLeft,
.loginRight {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.loginLogo {
    font-weight: 600;
    font-size: 42px;
    margin-bottom: 16px;
}

.loginLeftText {
    font-size: 24px;
}

.usedFramework {
    display: flex;
    align-items: center;
    justify-content: start;
    margin-top: 12px;
}

.reactImage {
    margin-right: 36px;
}

.firebaseImage {
    margin: 0;
    padding: 0;
}

.loginBox {
    height: 300px;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    box-shadow: 0px 0px 15px -5px #777777;
    border-radius: 10px;
    padding: 48px 0;
    background-color: white;
    position: relative;
}

.loginInput {
    border: 1px black solid;
    border-radius: 16px;
    align-self: center;
    height: 50px;
    width: 70%;
    background-color: white;
    padding: 0 8px;
    font-size: 24px;
    margin-bottom: 16px;

}

.loginInput:focus {
    outline: none;
}

.loginButton {
    width: 30%;
    align-self: center;
    font-size: 16px;
    border: 1px black solid;
    cursor: pointer;
    margin-bottom: 24px;
    padding: 8px 16px;
    border-radius: 8px;
    color: black;
}

.loginButton:disabled {
    width: 30%;
    align-self: center;
    font-size: 16px;
    cursor: not-allowed;
    border: 1px black solid;
    margin-bottom: 24px;
    padding: 8px 16px;
    border-radius: 8px;
    color: white;
    background-color: #c2c2c2;
}

.loginRegisterButton {
    width: 30%;
    align-self: center;
    font-size: 16px;
    border: 1px black solid;
    cursor: pointer;
    padding: 8px 16px;
    border-radius: 8px;
}


.button {
    transition: all 0.3s;
}

.register,
.register a {
    display: flex;
    flex-direction: column;
}

.registerText {
    align-self: center;
    font-size: 12px;
    margin-bottom: 5px;
}

.loginInput.valid::after {
    content: '✓';
    color: green;
    font-weight: bold;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
}

.backToLogin {
    font-size: 16px;
    position: absolute;
    left: 20px;
    bottom: 10px;
    border-bottom: 1px solid gray;
}